"use client";
import Image from "next/image";
import React from "react";
import AnimatedTagline from "./AnimatedTagline"; 

export default function HeroSection() {
  return (
    <section className="w-full bg-white py-20">
      <div className="max-w-7xl mx-auto px-6 md:px-12 xl:px-20">
        <div className="flex flex-col-reverse xl:flex-row items-center gap-24">
          {/* LEWA STRONA */}
          <div className="w-full xl:w-5/12 text-center xl:text-left pt-6 pb-6">
            {/* Badge */}
            <div className="grid grid-cols-2 sm:grid-cols-3 gap-3 max-w-md mx-auto xl:mx-0 mb-6">
                <Badge icon="controller" text="Game Crafting" color="from-blue-600 to-purple-600" />
                <Badge icon="brush" text="Design Code" color="from-purple-600 to-pink-500" />
                <Badge icon="cpu" text="Tech Savvy" color="from-pink-500 to-indigo-500" />
                <Badge icon="globe" text="Web Dev" color="from-indigo-500 to-blue-600" />
                <Badge icon="lightbulb" text="Creative Thinking" color="from-yellow-400 to-orange-500" />
                <Badge icon="pen" text="UI/UX" color="from-green-500 to-teal-500" />
            </div>



            {/* Tagline */}
            <p className="text-gray-500 text-lg font-light mb-6">
              Crafting digital experiences — playable or clickable.
            </p>

            {/* Nagłówek */}
            <div className="mb-8">
                <AnimatedTagline />
                </div>







            {/* Przyciski */}
            <div className="flex flex-col sm:flex-row justify-center xl:justify-start gap-4 mt-6">
              <a
                href="/projects"
                className="inline-flex items-center justify-center px-6 py-3 text-sm font-semibold border border-black text-black rounded-lg hover:bg-black hover:text-white transition"
              >
                <i className="bi bi-code-slash me-2" /> Explore Projects
              </a>
              <a
                href="/resume"
                className="inline-flex items-center justify-center px-6 py-3 text-sm font-semibold bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition"
              >
                <i className="bi bi-file-earmark-person-fill me-2" /> View Resume
              </a>
            </div>
          </div>

          {/* PRAWA STRONA */}
          <div className="w-full xl:w-7/12 flex justify-center relative">
            <div className="relative bg-gradient-to-r from-blue-600 to-pink-500 p-4 rounded-[2rem]">
              <Image
                src="/images/avatar-3.png"
                alt="Krzysztof Kaszuba avatar"
                width={400}
                height={400}
                className="rounded-lg z-10 relative"
              />
              <div className="absolute -top-10 -left-10 z-0 hidden sm:block">
                <Dots position="topLeft" />
              </div>
              <div className="absolute -bottom-10 -right-10 z-0 hidden sm:block">
                <Dots position="bottomRight" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

function Dots({ position }: { position: string }) {
  return (
    <svg width="60" height="120" viewBox="0 0 60 120" fill="none" xmlns="http://www.w3.org/2000/svg">
      {[...Array(2)].map((_, col) =>
        [...Array(5)].map((_, row) => (
          <circle
            key={`${col}-${row}`}
            cx={col * 30 + 10}
            cy={row * 24 + 10}
            r="5"
            fill="#3B82F6"
          />
        ))
      )}
    </svg>
  );
}
